@media screen and (max-width:1700px) {
	.section-map {
		padding-top: 188px;
		overflow: hidden;
	}

	.container-map {
		width: 1400px;
	}

	.secmap-clouds {
		left: 0;
		top: 225px;
		width: 1920px;
		left: 50%;
		margin-left: -871px;
	}

	.secmap-title {
		letter-spacing: 0.3px;
	}

	.secmap-info {
		width: 428px;

		margin-top: 75px;
	}

	.secmap-info-container {
		width: 311px;
	}

	.secmap-info-top {
		padding-top: 59px;

		padding-bottom: 40px;
	}

	.secmap-info-innner {
		padding-bottom: 0;
	}

	.secmap-item {
		padding-left: 30px;
	}

	.secmap-item+.secmap-item {
		margin-top: 29px;
	}

	.secmap-list {
		margin-top: 12px;

		list-style: none;
	}

	.secmap-list-item+.secmap-list-item {
		margin-top: 10px;
	}

	.secmap-item__title {
		letter-spacing: -.1px;

		font-size: 19px;
	}

	.secmap-list-item__city {
		letter-spacing: -.3px;
	}

	.secmap-list-item__street {
		letter-spacing: -.34px;

		line-height: 25px;
	}

	.secmap-time__item {
		letter-spacing: 0.18px;

		font-size: 17px;

		font-weight: 300;

		line-height: 24.46px;
	}

	.secmap-info-bottom {
		padding-top: 49px;
	}

	.secmap-info-bottom__phone {
		padding-left: 21px;

		letter-spacing: -.5px;

		font-size: 23px;
	}

	.secmap-form {
		width: 250px;
	}

	.secmap-info-input {
		height: 69px;

		margin-top: 19px;
	}

	.btn-v2 .btn-v2__inner {
		min-height: 77px;
	}

	.secmap-info-bottom {
		padding-bottom: 72px;
	}

	.container-map {
		padding-bottom: 85px;
	}
}

@media screen and (max-width:1430px) {
	.section-map {
		margin-top: 0;

		padding-top: 131px;
	}

	.secmap-clouds {
		top: 170px;
		margin-left: -961px;
	}

	.secmap-info {
		margin-top: 47px;
	}

	.secmap-item {
		margin-right: -20px;

		padding-left: 34px;
	}

	.container-map {
		width: 1207px;
	}
}

@media screen and (max-width:1100px) {
	.section-map {
		padding-top: 120px;
	}

	.secmap-title {
		font-size: 40px;

		line-height: 45px;
	}

	.container-map {
		width: 962px;
	}

	.secmap-info {
		margin-top: 73px;
	}
}

@media screen and (max-width:835px) {
	.container-map {
		width: 606px;

		max-width: calc(100% - 34px);

		padding-bottom: 98px;
	}

	.secmap-title {
		font-size: 35px;

		line-height: 40px;

		br {
			display: none;
		}
	}

	.secmap-info {
		margin-top: 54px;
	}

	.section-map {
		padding-top: 91px;
	}
}



@media screen and (max-width:586px) {
	.secmap-clouds {
		display: none;
	}

	.secmap-bg {
		position: static;

		order: 99;
	}

	.secmap-map {
		position: static;

		height: 319px;
	}

	.section-map {
		display: flex;

		flex-direction: column;
	}

	.container-map {
		max-width: 100%;
	}

	.secmap-title {
		font-size: 20px;
		line-height: 26px;
	}

	.secmap-info {
		margin-top: 25px;
		width: 100%;
	}

	.secmap-info-container {

		max-width: calc(100% - 40px);
	}

	.secmap-info-top {
		padding-top: 55px;
	}

	.secmap-item {
		padding-left: 25px;
		padding-right: 0;
		margin-right: 0;
	}

	.secmap-item__title {
		font-size: 17px;
	}

	.secmap-list-item__city {
		font-size: 15px;
		line-height: 22.1px;
		letter-spacing: 0.3px;

	}

	.secmap-list-item__street {
		font-size: 15px;
		line-height: 22.1px;
		letter-spacing: 0.3px;
	}

	.secmap-time {
		margin-top: 5px;
	}

	.secmap-time__item {

		font-size: 15px;
		line-height: 22.1px;
		letter-spacing: 0.3px;
	}

	.secmap-info-bottom__phone {
		font-size: 20px;
		line-height: 1;
		padding-left: 23px;
		letter-spacing: 0px;
	}

	.secmap-info-bottom {
		padding-top: 46px;

		.secmap-info-container {
			width: 228px;
		}
	}

	.secmap-info-input {
		height: 61px;
	}

	.btn-v2 .btn-v2__inner {
		min-height: 68px;
	}

	.container-map {
		padding-bottom: 0;
	}

	.secinfo-border {
		display: none;
	}

	.secmap-info-bottom {
		padding-bottom: 67px;
	}
}